import type { InputHTMLAttributes } from 'react';

import { css, styled } from '../../utils/styled';
import { withInputContainerStyle, withInputFieldStyle, withInputSizeVariants } from '../../utils/styles';
import type { InputSizeVariants, InputStyleProps } from '../../utils/styles';
import { color, modifier, spacing } from '../../utils/theme';
import type { FlexProps } from '../Flex';
import { Flex } from '../Flex';

export const Container = styled.div`
    width: 100%;
    display: grid;
    gap: ${spacing(1)};
    grid-template-columns: minmax(50px, 1fr); /* This ensures Input doesn't overflow 100% */
`;

interface InputInterface extends InputHTMLAttributes<HTMLInputElement>, InputStyleProps {
    inputSize: InputSizeVariants;
}

export const StyledInput = styled.input<InputInterface>`
    ${withInputFieldStyle};

    border: 0;
    min-width: 50px;
    width: 100%;
    padding: 0;
    display: block;

    ${withInputSizeVariants};
`;

export const InputIcon = styled.div`
    line-height: 1;
    font-size: 0;
`;

type InputContainerProps = {
    hasPrefix?: boolean;
    hasSuffix?: boolean;
    disabled?: boolean;
    error?: boolean;
};

export const InputContainer = styled(Flex)<FlexProps & InputContainerProps>`
    ${withInputContainerStyle};

    align-items: center;
    padding: ${spacing(0, 3)};
    color: ${color('content.secondary')};

    &:focus-within,
    [data-dev-state='focus'] & {
        color: ${color('primary.500')};
    }

    // All items should preserve their width
    > * {
        flex-shrink: 0;
    }

    // Except input
    > ${StyledInput} {
        flex-shrink: 1;
    }

    ${modifier(
        'hasPrefix',
        css`
            padding-left: ${spacing(2)};
        `,
    )};

    ${modifier(
        'hasSuffix',
        css`
            padding-right: ${spacing(2)};
        `,
    )};
`;
