import { css, styled } from '../../utils/styled';
import { color, modifier, radius, spacing, transitionTime, variants } from '../../utils/theme';
import { Pressable } from '../Pressable';
import type { Appearance } from './IconButton';

type IconButtonProps = {
    appearance: Appearance;
    active?: boolean;
    disabled?: boolean;
    isLoading: boolean;
};

const variantColorStyles = css`
    ${variants('appearance', {
        primary: css`
            padding: 6px;
            font-size: 20px;

            // hover
            &:hover:not([disabled]) {
                background-color: ${color('surface.primary100')};
                color: ${color(`primary.500`)};
            }

            // pressed
            &:active:not([disabled]),
            &:focus:active:not([disabled]) {
                background-color: ${color(`primary.200`)};
                color: ${color(`primary.500`)};
            }

            // focus
            &:focus:not([disabled]) {
                background-color: ${color(`surface.primary50`)};
                color: ${color(`primary.500`)};
            }

            // activated
            ${modifier(
                'active',
                css`
                    background-color: ${color(`surface.primary50`)};
                    color: ${color(`primary.500`)};
                `,
            )}
        `,
        secondary: css`
            padding: ${spacing(1)};
            font-size: 16px;
            color: ${color(`content.secondary`)};

            // hover
            &:hover:not([disabled]) {
                background-color: ${color(`border.secondary`)};
                color: ${color(`content.primary`)};
            }

            // pressed
            &:active:not([disabled]),
            &:focus:active:not([disabled]) {
                background-color: ${color(`border.primary`)};
                color: ${color(`content.primary`)};
            }

            // focus
            &:focus:not([disabled]) {
                background-color: ${color(`surface.secondary`)};
                color: ${color(`content.primary`)};
            }

            // activated
            ${modifier(
                'active',
                css`
                    background-color: ${color(`surface.secondary`)};
                    color: ${color(`content.primary`)};
                `,
            )}
        `,
        verified: css`
            padding: ${spacing(1)};
            font-size: 16px;
            color: ${color(`content.secondary`)};

            // hover
            &:hover:not([disabled]) {
                background-color: ${color(`surface.yellow100`)};
                color: ${color(`content.primary`)};
            }

            // pressed
            &:active:not([disabled]),
            &:focus:active:not([disabled]) {
                background-color: ${color(`yellow.200`)};
                color: ${color(`content.primary`)};
            }

            // focus
            &:focus:not([disabled]) {
                background-color: ${color(`surface.yellow100`)};
                color: ${color(`content.primary`)};
            }

            // activated
            ${modifier(
                'active',
                css`
                    background-color: ${color(`surface.yellow50`)};
                    color: ${color(`content.primary`)};
                `,
            )}
        `,
        reviewed: css`
            padding: ${spacing(1)};
            font-size: 16px;
            color: ${color(`content.secondary`)};

            // hover
            &:hover:not([disabled]) {
                background-color: ${color(`surface.green100`)};
                color: ${color(`content.primary`)};
            }

            // pressed
            &:active:not([disabled]),
            &:focus:active:not([disabled]) {
                background-color: ${color(`green.200`)};
                color: ${color(`content.primary`)};
            }

            // focus
            &:focus:not([disabled]) {
                background-color: ${color(`surface.green100`)};
                color: ${color(`content.primary`)};
            }

            // activated
            ${modifier(
                'active',
                css`
                    background-color: ${color(`surface.green50`)};
                    color: ${color(`content.primary`)};
                `,
            )}
        `,
    })}
`;

export const Container = styled(Pressable)<IconButtonProps>`
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    color: ${color(`content.primary`)};
    transition: background-color ${transitionTime()}, color ${transitionTime()};
    border: 0;
    border-radius: ${radius('md')};
    text-decoration: none;

    ${variantColorStyles}

    // disabled
    &:disabled {
        cursor: not-allowed;
        color: ${color('interactive.primary.disabled')};

        ${modifier(
            'isLoading',
            css`
                color: initial;
                cursor: auto;
                ${variantColorStyles}
            `,
        )}
    }
`;
